import androidx.compose.material.Typography
import androidx.compose.runtime.Composable
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.font.FontFamily
import androidx.compose.ui.text.font.FontWeight
import kotlinproject.composeapp.generated.resources.Res
import kotlinproject.composeapp.generated.resources.stalinist_one_regular
import org.jetbrains.compose.resources.Font

@Composable
fun CosmoFontFamily() = FontFamily(
    Font(Res.font.stalinist_one_regular, weight = FontWeight.Normal)
)

@Composable
fun CosmoTypography() = Typography().run {
    val fontFamily = CosmoFontFamily()
    copy(
        body1 = body1.copy(fontFamily = fontFamily, color = Color.White)
    )
}