@Composable
fun TapAreaComponent(modifier: Modifier = Modifier, onCosmoTap: () -> Unit) {
    Box(
        modifier = modifier
            .clip(RoundedCornerShape(16.dp))
            .border(
                BorderStroke(0.5.dp, Color(0xff878891)),
                RoundedCornerShape(16.dp)
            )
    ) {
        Column {
            Text(
                modifier = Modifier.fillMaxWidth().padding(vertical = 20.dp)
                    .padding(horizontal = 20.dp),
                textAlign = TextAlign.Center,
                text = "TAP TO EARN",
                style = MaterialTheme.typography.body1,
                color = Color(0xff878891)
            )
            Image(
                modifier = Modifier.fillMaxSize().clickableNoRipple {
                    onCosmoTap()
                },
                alignment = Alignment.BottomCenter,
                painter = painterResource(Res.drawable.cosmo),
                contentDescription = ""
            )
        }

    }

}