package ru.patstudio.quizzz

import CosmoTypography
import androidx.compose.foundation.Image
import androidx.compose.foundation.background
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.foundation.layout.padding
import androidx.compose.foundation.layout.size
import androidx.compose.material.MaterialTheme
import androidx.compose.material.Scaffold
import androidx.compose.runtime.*
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.paint
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.unit.dp
import kotlinproject.composeapp.generated.resources.Res
import kotlinproject.composeapp.generated.resources.commet
import kotlinproject.composeapp.generated.resources.stars
import org.jetbrains.compose.resources.painterResource
import org.jetbrains.compose.ui.tooling.preview.Preview
import ru.patstudio.quizzz.ds.components.BottomMenuNavigationComponent
import ru.patstudio.quizzz.ds.components.TapAreaComponent
import ru.patstudio.quizzz.ds.components.TapProfileComponent

@OptIn(ExperimentalStdlibApi::class)
@Composable
@Preview
fun MainScreen(
    modifier: Modifier
) {
    MaterialTheme(

        typography = CosmoTypography()
    )
    {
        var tapCount by remember { mutableStateOf(0) }
        Scaffold(
            modifier = modifier
        ) {
            Column(
                Modifier.fillMaxSize()
                    .background(color = Color(0xFF12151e)),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Box(
                    modifier = Modifier
                        .fillMaxSize()
                        .paint(
                            painterResource(Res.drawable.stars),
                            contentScale = ContentScale.FillBounds
                        )
                ) {
                    Column {
                        TapProfileComponent(
                            modifier = Modifier.fillMaxWidth(),
                            tapCount = tapCount
                        )
                        Image(
                            modifier = Modifier.padding(end = 20.dp).fillMaxWidth().size(40.dp),
                            alignment = Alignment.CenterEnd,
                            painter = painterResource(Res.drawable.commet),
                            contentDescription = ""
                        )
                        TapAreaComponent(
                            modifier = Modifier.padding(horizontal = 20.dp, vertical = 10.dp)
                                .weight(1f),
                            onCosmoTap = {

                                tapCount += 1
                            }
                        )
                        BottomMenuNavigationComponent(
                            modifier = Modifier.padding(
                                horizontal = 20.dp,
                                vertical = 10.dp
                            )
                        )
                    }
                }
            }

        }
    }
}